import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '@core/data-access/services/auth.service';
import { Agents, ClientUser } from '@core/models';
import { Agent } from '@core/models/place-ad.model';
import { StorageService } from '@core/services/storage.service';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';
import { PlaceAdService } from '@feature/place-ad/place-ad.service';
import { ButtonType } from '@shared/components/button/button.component';
import { Observable, tap } from 'rxjs';

@Component({
    selector: 'findqo-place-ad-show-agents-list',
    templateUrl: './place-ad-show-agents-list.component.html',
    styleUrls: ['./place-ad-show-agents-list.component.scss'],
    standalone: false
})
export class PlaceAdShowAgentsListComponent {
	constructor(
		private formBuilder: FormBuilder,
		private placeAdFacade: PlaceAdFacade,
		private placeAdService: PlaceAdService,
		private storageService: StorageService,
		private authService: AuthService
	) {
		this.user = this.authService.user();
	}

	user;

	@Output() formSubmittedEvent = new EventEmitter<{ formSubmitted: boolean; agentId?: string }>();
	@Output() agentChange = new EventEmitter<{ agentId: string }>(null);
	@Input() set selectedAgent(data) {
		if (data) {
			this.agentsListForm.patchValue({ agentsList: data?.id });
			this.handleAgent({ value: data?.id });
		}
	}

	agentsList: Agent[];
	searchAgentsName = new FormControl('');
	user$: Observable<ClientUser>;

	readonly buttonType = ButtonType;

	agentsListForm: FormGroup = this.formBuilder.group({
		agentsList: ['', Validators.required],
	});

	resetSearchControl(): void {
		this.searchAgentsName.patchValue('');
	}

	handleAgent($event): void {
		const SELECTED_AGENT_ID = $event.value;
		this.agentChange.emit({ agentId: SELECTED_AGENT_ID });

		if (SELECTED_AGENT_ID !== '0') {
			this.placeAdService.addAgentsId(SELECTED_AGENT_ID);
			this.storageService.setItem('agentId', SELECTED_AGENT_ID);
		} else {
			this.placeAdService.addAgentsId(null);
			this.storageService.setItem('agentId', null);
		}
	}

	ngOnInit(): void {
		this.placeAdFacade.getAgents$().subscribe((res) => {
			this.agentsList = res;
			if (!this.agentsListForm.get('agentsList').value) {
				const CURRENT_USER_AGENT = res.find((it) => it.userId === this.user.id);
				this.agentsListForm.patchValue({ agentsList: CURRENT_USER_AGENT?.id });
				this.handleAgent({ value: CURRENT_USER_AGENT?.id });
			}
		});
	}
}
